<template>
  <div class="box-border">
    <div :class="['w-full mx-auto h-11 bg-lightBlue-500 rounded-b-md py-1 text-center flex justify-between items-center font-mono font-bold px-2 box-border text-white transform', {'animate__animated animate__fadeIn': controlAnimate}]">
      <van-icon name="arrow-left" @click="onClickLeft" />
      <h1>个人信息</h1>
      <span @click="onClickRight">保存</span>
    </div>
    <div class="px-3 box-border">
      <div :class="['information-list py-4 rounded-2xl box-border px-3 shadow-xl font-mono font-light', { 'animate__animated animate__flipInX ': controlAnimate}]">
        <van-cell title="头像" is-link @click="editIcon" class="flex items-center text-lg mb-3">
          <template #default>
            <div class="img flex justify-end items-center">
              <img :src="userInfo.photo" alt=""  class="w-20 h-20 rounded-full"/>
            </div>
          </template>
        </van-cell>
        <van-cell title="昵称" is-link @click="editInfo" v-model="userInfo.name" class=" text-lg mb-3">
        </van-cell>
        <van-cell title="性别" is-link @click="editGender" class=" text-lg mb-3">
          <template #default>
            {{ genderArr[userInfo.gender] }}
          </template>
        </van-cell>
        <van-cell title="生日" is-link @click="editBirthday" class=" text-lg mb-3">
          <template #default>
            {{ userInfo.birthday }}
          </template>
        </van-cell>
      </div>
    </div>
    <!-- 点击展开昵称修改框 -->
    <van-popup v-model="inputShow" class="rounded-2xl w-5/6 h-11 ">
      <div class="flex justify-between overflow-hidden">
        <input class="rounded-lg pl-8 font-mono font-bold text-gray-500" type="text" v-model="userInfo.name" />
        <van-button type="info" @click="editName">确认</van-button>
      </div>
    </van-popup>
    <!-- 点击展开性别选择 -->
    <van-popup
      v-model="genderShow"
      position="bottom"
      round
      :style="{ height: '30%' }"
    >
      <van-picker
        title="我的性别"
        show-toolbar
        :columns="genderList"
        visible-item-count="3"
        :default-index="userInfo.gender"
        @confirm="onConfirm"
        @cancel="onCancel"
      />
    </van-popup>
    <!-- 点击展开生日选择 -->
    <van-popup
      v-model="birthdayShow"
      position="bottom"
      :style="{ height: '30%' }"
    >
      <van-datetime-picker
        v-model="currentDate"
        type="date"
        title="选择年月日"
        columns="5"
        :min-date="minDate"
        :max-date="maxDate"
        @confirm="onTimeConfirm"
        @cancel="onTimeCancel"
      />
    </van-popup>
    <!-- 上传头像 -->
    <van-popup v-model="iconShow" class="w-64 h-52 rounded-xl overflow-hidden">
      <div class="flex flex-col justify-between items-center w-full h-full text-xl py-4" @click.stop>
          <div class="-my-2 pt-1">
            <van-uploader
              class=""
              :after-read="afterRead"
              :before-read="beforeRead"
            >
              <van-button icon="plus" class="border-0 font-sans text-lg whitespace-nowrap align-middle" before-read="beforeRead"
                >从相册中选择</van-button
              >
            </van-uploader>
          </div>
          <div class="w-full py-4 text-center ring-2 ring-gray-100">拍照</div>
          <div class="" @click="iconShow = false">取消</div>
      </div>
    </van-popup>
    <van-popup v-model="imgpreShow" class="w-64 h-96 rounded-b-xl overflow-hidden">
      <div class="shadow-lg px-1 pt-1">
        <img :src="iconUrl" alt="" />
      </div>
      <div class="flex justify-evenly items-center mt-6">
        <div class="w-20 h-20 rounded-full bg-rose-500 flex justify-center items-center font-mono text-white" @click="imgpreShow = false">取消</div>
        <div class="w-20 h-20 rounded-full bg-lightBlue-500 flex justify-center items-center font-mono text-white" @click="editImg">确认</div>
      </div>
    </van-popup>
  </div>
</template>
<script>
// 导入轻提示
import { Toast } from 'vant'
// 导入图片预览插件
import dayjs from 'dayjs'
// 导入获取用户信息接口
import {
  editUserInformation,
  editUserInfo
} from '../../../api/userInfo'
// 导入vuex
import { mapMutations, mapState } from 'vuex'
export default {
  name: 'userInfo',
  data () {
    return {
      inputShow: false,
      genderShow: false,
      birthdayShow: false,
      iconShow: false,
      imgpreShow: false,
      bindingName: '123',
      genderArr: ['男', '女'],
      genderList: ['男孩子', '女孩子'],
      minDate: new Date(1990, 0, 1),
      maxDate: new Date(2025, 10, 1),
      currentDate: '',
      userInfo: {},
      iconUrl: '',
      iconFile: '',
      controlAnimate: true
    }
  },
  created () {
    this.getInfo()
  },
  computed: {
    ...mapState(['editInfoList', 'editInfoList'])
  },
  mounted () {
    setTimeout(() => {
      this.controlAnimate = false
    }, 900)
  },
  methods: {
    async getInfo () {
      const res = await editUserInfo({
        target: this.userInfo.id
      })
      this.userInfo = res.data
    },
    // 修改头像
    editIcon () {
      this.iconShow = true
    },
    // 头像上传前
    beforeRead (file) {
      if (
        file.type !== 'image/jpeg' &&
        file.type !== 'image/png' &&
        file.type !== 'image/gif'
      ) {
        Toast('请上传 jpg/png/gif 格式图片')
        return false
      }
      return true
    },
    // 头像上传后
    afterRead (file) {
      // 此时可以自行将文件上传至服务器
      this.imgpreShow = true
      this.iconUrl = file.content
      this.iconShow = false
      this.iconFile = file.content.split(',')[1]
      console.log(file)
    },
    editImg () {
      this.userInfo.photo = this.iconUrl
      this.imgpreShow = false
    },
    onClickLeft () {
      this.$router.push('/user')
    },
    ...mapMutations(['editUserInfo']),
    // 保存按钮  需要发起请求
    async onClickRight () {
      Toast.loading({
        message: '加载中...',
        forbidClick: true
      })
      const res = this.iconFile ? await editUserInformation({
        name: this.userInfo.name,
        gender: this.userInfo.gender,
        birthday: this.userInfo.birthday,
        photo: this.iconFile
      }) : await editUserInformation({
        name: this.userInfo.name,
        gender: this.userInfo.gender,
        birthday: this.userInfo.birthday
      })
      this.editUserInfo(res.data)
      Toast.success('修改成功')
      this.$router.push('/user')
      console.log(res.data)
    },
    // 点击出现昵称修改界面
    editInfo () {
      this.inputShow = true
    },
    // 修改昵称
    async editName () {
      this.inputShow = false
    },
    // 修改性别
    editGender () {
      this.genderShow = true
    },
    // 修改出生日期
    editBirthday () {
      this.birthdayShow = true
      this.currentDate = new Date(this.userInfo.birthday.split('-'))
    },
    // 性别选择器
    onConfirm (value, index) {
      this.userInfo.gender = index
      Toast('修改成功')
      this.genderShow = false
      console.log(index)
    },
    onCancel () {
      this.genderShow = false
    },
    // 时间选择器
    onTimeConfirm (value) {
      this.userInfo.birthday = dayjs(value).format('YYYY-MM-DD')
      Toast('修改成功')
      console.log(this.userInfo.birthday)
      this.birthdayShow = false
    },
    onTimeCancel () {
      this.birthdayShow = false
    }
  }
}
</script>
